import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Navigate } from "react-router-dom";

import Input from "../../../../components/Input/Input";
import { clearError, resetRegistered } from "../../store/authSlice";
import { login } from "../../api/loginFetch";
import Loading from "../../../../components/loading/Loading";
import PasswordResetForm from "./PasswordResetForm";

const SignInForm = () => {
  const dispatch = useDispatch();
  const { loading, isAuthenticated, loginError, registered, isVerificated } = useSelector(
    (state) => state.auth
  );

  const [formData, setFormData] = useState({
    username: "",
    password: "",
  });

  const [isForgotPassword, setIsForgotPassword] = useState(false);

  useEffect(() => {
    if (registered) dispatch(resetRegistered());
  }, [dispatch, registered]);

  const { username, password } = formData;

  const handleChange = (event) => {
    setFormData({ ...formData, [event.target.name]: event.target.value });
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    // console.log(error);
    dispatch(clearError());

    dispatch(login({ username, password }));
  };

  if (isAuthenticated) {
    return <Navigate to="/" />;
  }

  return (
    <div className="form-container sign-in">
      {isForgotPassword ? (
        <PasswordResetForm onBack={() => setIsForgotPassword(false)} />
      ) : (
        <form onSubmit={handleSubmit}>
          {isVerificated ? (
            <p
              style={{
                fontSize: "18px",
                color: "green",
                padding: "10px",
                backgroundColor: "#f4f4f4",
                border: "1px solid #ccc",
                borderRadius: "3px",
                textAlign: "center",
                margin: "20px 0",
              }}
            >
              !       
            </p>
          ) : (
            ""
          )}
          <h1></h1>
          <Input
            name="username"
            type="text"
            className={`form-control ${loginError ? "error-input" : ""}`}
            value={username}
            onChange={handleChange}
            placeholder=""
            required
            error={loginError}
          />
          <Input
            name="password"
            type="password"
            className={`form-control ${loginError ? "error-input" : ""}`}
            value={password}
            onChange={handleChange}
            placeholder=""
            required
            error={loginError}
          />
          <div className="social-icons">
            {/* <span>  </span>
            <a href="3" className="icon">
              <i className="fa-brands fa-google" style={{ color: "#B197FC" }}></i>
            </a> */}
          </div>
          <span onClick={() => setIsForgotPassword(true)} className="forgot-password"> ?</span>
          {loginError && (
            <div>
              <p className="error-message">
                {loginError === "No active account found with the given credentials"
                  ? "    "
                  : loginError}
              </p>
            </div>
          )}
          {loading ? <Loading /> : <button type="submit"></button>}
        </form>
      )}
    </div>
  );
};

export default SignInForm;
